import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)





//tab
const alist={
	listing:[]
}
//tab
const load = {
	loading:''
}

const navmin={
	loading:'',
	naving:'recomment',
	pageid:0,
	uniload:'',
	nonedata:''
}

//tab
const nonemin = {
	nonedata:''
}

//
const city = {
	citying:''
}


//
const state = {
	alist,
	load,
	navmin,
	nonemin,
	city,
}


export default new Vuex.Store({
	state,
	//
	mutations:{
		listmuta(state,listdata){
			state.alist = {
				listing:listdata
			}
		},
		//tabloading
		loadmuat(state,loading){
			state.load = {
				loading:loading
			}
		},
		//
		navmuta(state,pullobj){
			// console.log(pullobj)
			state.navmin = {
				loading:pullobj.loading,
				naving:pullobj.nav,
				pageid:pullobj.pageid,
				uniload:pullobj.uniload,
				nonedata:pullobj.nonedata
			}
		},
		//tab
		nonemuta(state,noneion){
			console.log(noneion)
			state.nonemin = {
				nonedata:noneion
			}
		},
		//
		citymuta(state,cityion){
			console.log(cityion)
			state.city = {
				citying:cityion
			}
		}
	}
})